<script>
import { mapState, mapActions } from "vuex";
import api from "../../utils/api";
export default {
  props: ["allProShow","cartNum"],
  data() {
    return {
      menuTop: [],

      menuLeft: [],
     
    };
  },
  created() {
    api.get("/menu?type=top", { _silent: true }).then(data => {
      // console.log(data);
      this.menuTop = data;
    });

    api.get("/menu?type=left", { _silent: true }).then(data => {
      // console.log(data);
      this.menuLeft = data;
    });
  
  },
  computed: {
    ...mapState("user", ["loggedIn", "user"])
  },
  methods: {
    ...mapActions("user", ["logout"]),
    confirmQuit() {
      this.$confirm("确认退出登录？")
        .then(() => {
          this.logout();
          this.$router.push("/login");
        })
        .catch(() => {});
    }
  }
};
</script>

