<script>
import appHead from "../components/Header";
import { mapState, mapActions } from "vuex";

import api from "../../utils/api";
// import swiper from "../components/Swiper";
import { Swiper, Slide } from "vue-swiper-component";

export default {
  data() {
    return {
      menuCarousel: [],
      menuIndex: [],
      carouselIndex: 0,
      cartNum: 0 //购物车数量
    };
  },
  created() {
    // 轮播图
    api.get("/menu?type=carousel", { _silent: true }).then(data => {
      // console.log(data);
      this.menuCarousel = data;
    });
    // 卡片
    api.get("/menu?type=index", { _silent: true }).then(data => {
      console.log(data);
      this.menuIndex = data;
    });
    //   先登录
    if (this.loggedIn) {
      api.get("/user/cart").then(data => {
        this.cartNum = data.length;
      });
    }
  },

  components: {
    "app-header": appHead,
    // "m-swipe": swiper,
    Swiper,
    Slide
  },
  computed: {
    ...mapState("user", ["user"])
  },
  methods: {
    ...mapActions("user", ["checklogin"])
  }
};
</script>

