import api from "../../../utils/api"

export default {
    namespaced: true,
    state: {
        loggedIn: false,
        user: {}
    },
    mutations: {
        LOGIN(state, data) {
            state.user = data;
            state.loggedIn = true;
            // 为以后请求统一设置
            console.log('为以后请求统一设置');

            api.defaults.headers.common["Token"] = data.token;
        },
        LOGOUT(state) {
            state.user = {};
            state.loggedIn = false;
            if (api.defaults.headers.common["Token"]) {
                api.defaults.headers.common["Token"] = '';
            }
        },

    },
    actions: {
        login({ commit }, data) {
            return api.post('/login', data).then(data => {
                commit("LOGIN", data)
            })
        },
        register({ commit }, data) {
            return api.post('/register', data).then(data => {
                commit("LOGIN", data)

            })
        },
        logout({ commit }) {
            commit('LOGOUT')

        },
        // 检查token,如果localStorage中的token带去后台验证后发现无效会自动使用notification报错
        check_login({ state }) {
            api.defaults.headers.common["Token"] = state.user.token || '';//这句不写，刷新会报错
          
        },
    
    },
}