// import Vue from "vue"
import  axios from "axios"
const api = axios.create({
    baseURL:"http://vip.chanke.xyz/mi",
})
api.interceptors.response.use(result=>{
    return result.data.data;
},err=>{
    if(err.config._silent){
        console.warn(err)
    }else{
        alert(err.response.data.errorMessage)
    }
})

export default api;